:root {
  font-size: 16px;
  font-family: "Open Sans";
  --theme: #24a113;
  --text-primary: #b6b6b6;
  --text-secondary: #ececec;
  --bg-primary: #23232e;
  --bg-secondary: #141418;
  --transition-speed: 600ms;
}

body {
  color: black;
  background-color: white;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  background: #1e1e24;
}

body::-webkit-scrollbar-thumb {
  background: #b6b6b6;
  background: var(--text-primary);
}

main {
  margin-left: 6.5rem;
  padding: 1rem;
}

.navbar {
  position: fixed;
  background-color: #23232e;
  background-color: var(--bg-primary);
  transition: width 600ms ease;
  z-index: 5;
}

.navbar-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.nav-item {
  width: 100%;
}

.nav-link {
  display: flex;
  align-items: center;
  height: 5rem;
  color: #b6b6b6;
  color: var(--text-primary);
  text-decoration: none;
  -webkit-filter: grayscale(100%) opacity(0.7);
          filter: grayscale(100%) opacity(0.7);
  transition: 600ms;
  transition: var(--transition-speed);
}

.nav-link:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: #141418;
  background: var(--bg-secondary);
  color: #ececec;
  color: var(--text-secondary);
}

.link-text {
  margin-left: 1rem;
  text-decoration: none;
  font-size: 1.2rem;
}

.nav-link svg {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.fa-primary {
  color: #24a113;
  color: var(--theme);
}

.fa-secondary {
  color: #24a113;
  color: var(--theme);
}

.fa-primary,
.fa-secondary {
  transition: 600ms;
  transition: var(--transition-speed);
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: #ececec;
  color: var(--text-secondary);
  background: #141418;
  background: var(--bg-secondary);
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: 600ms;
  transition: var(--transition-speed);
  margin: 0;
  margin-left: 0.75rem;
  width: 0.6em !important;
}

.logo-text {
  display: inline;
  position: absolute;
  top: 1.5rem;
  left: -999px;
  transition: 600ms;
  transition: var(--transition-speed);
  color: #ececec;
  color: var(--text-secondary);
}

.navbar:hover .logo svg {
  transform: rotate(-180deg);
  position: relative;
  right: 0.5rem;
}

.navbar:hover .icons {
  color: #24a113;
  color: var(--theme);
}

.icons {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: #ececec;
  color: var(--text-secondary);
  /* background: var(--bg-secondary); */
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.signout-btn {
  display: none;
}

/* Small screens */
@media only screen and (max-width: 600px) {
  .navbar {
    display: none;
  }
  .signout-btn {
    display: inline;
  }
}

