/* Large screens */
@media only screen and (min-width: 600px) {
  .navbar {
    top: 0;
    width: 6.5rem;
    height: 100vh;
  }

  .navbar:hover {
    width: 16rem;
  }

  .page-text {
    display: none;
    /* visibility: hidden; */
  }

  .navbar:hover .page-text {
    display: inline;
    color: #24a113;
    color: var(--theme);
    visibility: visible;
    opacity: 1;
    -webkit-animation: fade 0.85s;
            animation: fade 0.85s;
  }

  .navbar:hover .logo svg {
    margin-left: 11rem;
  }

  .navbar:hover .logo-text {
    left: 0px;
    color: #24a113;
    color: var(--theme);
  }

  .navbar:hover .signout-btn {
    display: inline;
    margin-left: 6rem;
    -webkit-animation: fade 3s;
            animation: fade 3s;
  }

  .nav-item:last-child {
    margin-top: auto;
  }

  .non-auth:last-child {
    margin-top: 0;
  }

  .icons {
    visibility: visible;
    margin: 0;
    padding: 0 !important;
    position: absolute;
    top: 2rem;
    left: 0.75rem;
  }

  .page-text {
    margin: 0;
    padding: 0;
    position: relative;
    bottom: -0.25rem;
    left: 4.5rem;
  }

  body {
    margin-right: 5rem;
    /* padding: 1rem; */
  }

  .base-container {
    margin-right: 6.5rem;
    padding-right: 2rem;
    padding-left: 8.5rem;
    padding-bottom: 2rem;
  }
}

@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

